import React from 'react'

// import { Container } from '../../styles/globalStyles'
import { ServicesContainer, Image } from '../../styles/servicesCardStyles'
import { DefaultSizes } from '../../styles/globalStyles'


const ServicesCard = (props) => {

    return(
        <ServicesContainer>
            <Image>
                <img src={props.image} alt="" />
            </Image>
            <DefaultSizes>
                <h2>{props.title}</h2>
                <p>{props.desc}</p>
            </DefaultSizes>
            
        </ServicesContainer>
        
        
    )
}

export default ServicesCard;