
import React from 'react'

import { Image, BriefContainer, BriefBackground } from '../../styles/videoCaptionStyles'
import { Container, DefaultSizes, VerticalPadding } from '../../styles/globalStyles'

// Context
// import {useGlobalStateContext, useGlobalDispatchContext} from '../../context/globalContext'

const ImageCaption = (props) => {
    return(
        <BriefBackground>
            <Image>
                <img src={props.image} alt =""/>
            </Image>
            <Container>
               <VerticalPadding>
                   <BriefContainer
                    animate={{y:0, opacity: 1}}
                    initial={{y:-72, opacity: 0}}
                    transition={{duration: 1, ease: [.6, .05, -.01, 0.9], delay:0.5}}
                >
                    <DefaultSizes>
                        <h1>{props.caption}</h1>
                        <p>{props.desc}</p>
                    </DefaultSizes>
                    
                </BriefContainer>
               </VerticalPadding>
                
                
                
                
            </Container>
        </BriefBackground>
        
    )
}

export default ImageCaption