import styled from 'styled-components';
import {motion} from 'framer-motion'


export const ServicesContainer = styled(motion.div)`
    
    margin: 0;
    
    width: 300px;
    display: flex;
    
    flex-direction: column;
    padding-bottom: 20px;
    
    h2 {
        
        &::before {
            content: '';
            display: block;
            margin-bottom: 10px;
            margin-top: 20px;
            width: 105px;
            height: 3px;
            background: linear-gradient(to right, #FFF0D5 0%, #FFDFFF 50%, #D9CEFF 100%);
        }
    }

    img {
        width: 60%;
        object-fit: cover;
    }
`

export const Image = styled(motion.div)``



